.scrollDiv {
    width: 640px;
    max-height: 70vh;
    overflow-y: scroll;
    border-radius: 0px;
    overflow-x: hidden;
}

.scrollDiv::-webkit-scrollbar {
    width: 8px;
    background: #2F2F37;
}

.scrollDiv::-webkit-scrollbar-thumb {
    background: #8585AD;
    border: 3px solid #8585AD;
}